import React from 'react';
import { Link, graphql } from 'gatsby';
import SEO from '../components/seo'
import DefaultLayout, { ConvertDate }  from '../components/layout';
import { kebabCase } from 'lodash';

const Tags = ({ pageContext, data }) => {
  const { tag } = pageContext;
  const { edges, totalCount } = data.allMarkdownRemark;
  const tagHeader = `${tag}: ${totalCount} post${
    totalCount === 1 ? '' : 's'}`;

  return (
    <DefaultLayout>
      <SEO title={tagHeader} description={tag} keywords={tag} />
      <div className="container">
        <h1>{tagHeader}</h1>
      </div>
      {edges.map(({ node }) => {
        return (
          <article className="post" key={node.fields.slug}>
            {node.frontmatter.img &&
              node.frontmatter.img.childImageSharp &&
              node.frontmatter.img.childImageSharp.fluid && (
                <Link
                  to={node.fields.slug}
                  className="post-thumbnail"
                  style={{
                    backgroundImage: `url(${node.frontmatter.img.childImageSharp.fluid.src})`,
                  }}
                />
              )}
            <div className="post-content">
              <div className="post-headeritem">
                <span className="post-date">
                  {
                    ConvertDate(node.frontmatter.date)
                  }&nbsp;&nbsp;—&nbsp;
                </span>
                <span className="post-words">
                  {node.timeToRead} {node.timeToRead == 1 ? 'minuto' : 'minutos'}
                </span>
              </div>
              <h2 className="post-title">
                <Link to={node.fields.slug}>{node.frontmatter.title}</Link>
              </h2>
              <p>{node.frontmatter.description} {node.excerpt.toString().replace('Generic badge', '')}</p>
              <div className="post-tags">
                {node.frontmatter.tags &&
                  node.frontmatter.tags.map(tag => (
                    <span key={tag}>
                      <Link className="tag" to={`/tags/${kebabCase(tag)}/`}>#{tag}</Link>
                    </span>
                  ))}
              </div>
            </div>
          </article>
        )
      })}
      <div className="container">
        <Link to="/tags" className="newer-posts">← Tags</Link>
      </div>
    </DefaultLayout>
  );
};

export default Tags

export const pageQuery = graphql`
  query($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          excerpt
          fields {
            slug
          }
          timeToRead
          frontmatter {
            date(formatString: "YYYY, MMM DD")
            title
            description
            tags
            img {
              childImageSharp {
                fluid(maxWidth: 3720) {
                  aspectRatio
                  base64
                  sizes
                  src
                  srcSet
                }
              }
            }
          }
        }
      }
    }
  }
`;
